@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
html,
body {
    margin: 0;
    padding: 0;
}

#root {
    background-color: #f3eeee;
    font-family: "Roboto", sans-serif;
}

.navbar {
    background-color: #184fb2;
    text-align: center;
    padding: 10px 20px;
}

.navbar h2 {
    text-align: center;
    color: white;
    font-size: 2rem;
    font-weight: bold;
    margin: 0;
    padding: 0  ;
}

li {
    color: black;
    padding: 5px;
}

li label {
    font-weight: 600;
    cursor: pointer;
}

li label:hover {
    text-decoration: underline;
}

.movement-history {
    border: 1px solid #4169e1bf;
}

.history-title {
    background: #4169e1bf;
    padding: 0.5rem;
    color: white;
}

.history-title h3 {
    margin: 0;
}

.history-body {
    list-style: none;
    padding-left: 15px;
}

.board {
    display: grid;
    grid-template-columns: -webkit-min-content -webkit-min-content -webkit-min-content;
    grid-template-columns: min-content min-content min-content;
    margin: 10px;
    justify-content: center;
}

.square {
    background-color: white;
    border: 5px solid #4169e1bf;
    font-size: 10vw;
    line-height: 10vw;
    font-weight: 800;
    text-align: center;
    height: 10vw;
    width: 10vw;
}

.square.X {
    color: #0fd10f;
}

.square.O {
    color: #e58a8a;
}

button.button-player {
    background-color: #b0aeb8b8;
    color: white;
    font-size: 1.2rem;
    padding: 0.5rem;
    margin: 0.3rem 0;
    border: none;
    border-radius: 0;
    width: 100%;
}

.button-player.active {
    background-color: royalblue;
}

.players-board {
    margin-bottom: 10px;
}

.players-board label {
    font-weight: 600;
}

.app {
}

.main-content {
    min-height: 83vh;
}

.winner-game {
    margin-bottom: 10px;
}

.footer {
    background-color: #f3eeee;
    text-align: center;
    padding: 15px 0;
    font-weight: bold;
    border-top: 1px solid #718fe9;
}

.footer a{
    font-size: 1.2rem;
    font-weight: bold;
    text-decoration: none;
    color: #184fb2;
}
